/**
 * Auth 
 */
import base from '@/api/base'; // 
import http from '@/utils/http'; // httpaxios
import {
  LoginParams,
  RegisterParams,
  RegisterAdminParams
} from '@/types/views/auth.interface';
// import Qs from 'qs'; // qspost

// 
export const userAuth = {
  // 
  login(params: LoginParams) {
    return http.post(`${base.v1}/login`, params);
  },
  // 
  register(params: RegisterParams) {
    return http.post(`${base.v1}/register`, params);
  }
};

// 
export const adminAuth = {
  // 
  login(params: LoginParams) {
    return http.post(`${base.v1}/admin/login`, params);
  },
  // 
  register(params: RegisterAdminParams) {
    return http.post(`${base.v1}/admin/register`, params);
  }
};
