/**
 * axios 
 * 
 */
import axios from 'axios';
import httpConfig from '@/config/httpConfig'; //  axios 
import router from '@/router';
import store from '@/store';
import { LOGIN_OUT, UPDATE_NETWORK } from '@/store/mutation-types';

/**
 * 
 */
const isAdmin = () => {
  return (
    router.currentRoute.path.match(/^\/admin$/) ||
    router.currentRoute.path.match(/^\/admin\//)
  );
};

/**
 *  /
 * 
 */
const toLogin = () => {
  router.replace({
    path: isAdmin() ? '/admin/login' : '/login',
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
};

/**
 *  loading...
 */
const startLoading = () => {
  // 
  // store.state.overlay = true;
  store.state.loading = true;
  // store.state.skeletonLoader = true;
};

/**
 *  loading...
 */
const endLoading = () => {
  // 
  // store.state.overlay = false;
  store.state.loading = false;
  // store.state.skeletonLoader = false;
};

// showFullScreenLoading() tryHideFullScreenLoading() 
//  needLoadingRequestCountshowFullScreenLoading needLoadingRequestCount + 1
// tryHideFullScreenLoading()needLoadingRequestCount - 1
// needLoadingRequestCount 0  loading

// 
let needLoadingRequestCount = 0;

/**
 * 
 */
const showFullScreenLoading = () => {
  needLoadingRequestCount === 0 && startLoading();
  needLoadingRequestCount++;
};

/**
 * 
 */
const tryHideFullScreenLoading = () => {
  if (needLoadingRequestCount <= 0) return;
  needLoadingRequestCount--; // 
  needLoadingRequestCount === 0 && endLoading();
};

/**
 * 
 */
const tip = (message: string) => {
  // 
  // process.env.NODE_ENV !== 'production' && console.log(message);
  return message;
  // 
  // ...
};

/**
 * 
 * @param {number} status - 
 */
const errorHandler = (status: number, message: string) => {
  switch (status) {
    case 400:
      // 
      tip(`${status}`);
      break;

    case 401:
      // 
      tip(`${status}`);
      // 
      toLogin();
      break;

    case 403:
      // 
      // token
      // a. 
      tip(`${status}`);
      // b. tokenvuextoken /
      store.commit({
        type: LOGIN_OUT,
        isAdmin: isAdmin() ? true : false
      });
      // c. 
      setTimeout(() => {
        toLogin();
      }, 1000);
      break;

    case 404:
      // 
      tip(`${status}`);
      break;

    case 405:
      // 
      tip(`${status}`);
      break;

    case 408:
      // 
      tip(`${status}`);
      break;

    case 500:
      // 
      tip(`${status}`);
      break;

    case 501:
      // 
      tip(`${status}`);
      break;

    case 502:
      // 
      tip(`${status}`);
      break;

    case 503:
      // 
      tip(`${status}`);
      break;

    case 504:
      // 
      tip(`${status}`);
      break;

    case 505:
      // HTTP
      tip(`${status}HTTP`);
      break;

    default:
      // ,
      tip(`${status}${message}`);
  }
};

/**
 *  axios 
 */
const instance = axios.create(httpConfig);
//  post 
instance.defaults.headers.post['Content-Type'] =
  'application/x-www-form-urlencoded';

/**
 * 
 * 0.  loading...
 * 1. token  /
 */
instance.interceptors.request.use(
  config => {
    // 

    // 0-1.  loading... 
    showFullScreenLoading();

    // 1. token /
    // token
    // token localStorage / sessionStorage  cookie vuextoken
    // vuextokentokenvuextoken;
    // a. vuextoken
    // b. tokentoken /  / token
    // c. tokentoken
    const token = isAdmin()
      ? store.state.Auth.adminToken
      : store.state.Auth.userToken;
    token && (config.headers.Authorization = token);

    //  config 
    return config;
  },
  error => {
    // 
    return Promise.reject(error);
  }
);

/**
 * 
 * 0.  loading...
 */
instance.interceptors.response.use(
  response => {
    // 2xx
    // 0-2.  loading... 
    tryHideFullScreenLoading();

    // 
    // 200
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    // 2xx
    // 0-3.  loading... 
    tryHideFullScreenLoading();

    // 
    if (error.response) {
      // 
      errorHandler(error.response.status, error.response.statusText);
      return Promise.reject(error);
    } else if (!navigator.onLine) {
      // 
      // state network false
      // network app.vue
      store.commit(UPDATE_NETWORK, false);
    } else {
      return Promise.reject(error);
    }
  }
);

export default instance;
