/**
 *  mutations
 */
import {
  UPDATE_TITLE,
  UPDATE_BREADCRUMBS,
  UPDATE_NETWORK
} from './mutation-types';
import RootState from '@/types/store/rootState.interface'; //  state 

export default {
  // 
  [UPDATE_TITLE](state: RootState, title: string) {
    state.title = title;
    document.title = `${title} - UXDCode`; //  document  titleVuex
  },

  // 
  [UPDATE_BREADCRUMBS](state: RootState, breadcrumbs: object[]) {
    state.breadcrumbs = breadcrumbs;
  },

  // 
  [UPDATE_NETWORK](state: RootState, network: boolean) {
    state.network = network;
  }
};
