/**
 * Vue Router 
 */
import Vue from 'vue';
import VueRouter from 'vue-router';
import routes from './routes';
import store from '@/store';
import { LOGIN_IN, UPDATE_NETWORK } from '@/store/mutation-types';
import { getToken } from '@/utils/common';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  // 
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      //  / 
      return savedPosition;
    } else {
      // 
      if (from.meta.keepAlive) {
        from.meta.savedPosition =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
      }
      //  / 
      return { x: 0, y: to.meta.savedPosition || 0 };
    }
  }
});

/**
 * 
 * 0.  loading...
 * 1.   vuex  token / network / state
 *    vuex 
 *   /  localStorage / sessionStorage token 
 * 2.  document  title / Vuex
 * 3. 
 */
router.beforeEach((to, from, next) => {
  // 0-1.  loading... 
  store.state.overlay = true;
  // store.state.loading = true;
  store.state.skeletonLoader = true;

  // 
  const isAdmin = to.path.match(/^\/admin$/) || to.path.match(/^\/admin\//);

  // 1-1.  token /  localStorage / sessionStorage token 
  // a. //token sessionStorage  token
  // b. /token sessionStorage  token

  /* store.commit({
    type: LOGIN_IN,
    isAdmin: isAdmin ? true : false,
    token: isAdmin ? getToken('adminToken') : getToken() //  'null' 
  }); */
  // localStorage / sessionStorage  string 
  // //
  //  sessionStorage  token 
  //  getToken('XXX')  nullobject 
  //  vuex  LOGIN_IN  setToken('XXX', null)  sessionStorage  sessionStorage  string  nullobject  'null'string
  //  sessionStorage  token  'null'  null if (!token)  ===>  if (!'null')  ===>  false if (!null)  ===>  true 

  // 
  //  getToken('XXX') null
  // / vuex  LOGIN_INsessionStorage  token 
  //  
  if (isAdmin && getToken('adminToken')) {
    //  && getToken('adminToken') !== null
    store.commit({
      type: LOGIN_IN,
      isAdmin: true,
      token: getToken('adminToken')
    });
  } else if (!isAdmin && getToken()) {
    //  && getToken() !== null
    store.commit({
      type: LOGIN_IN,
      isAdmin: false,
      token: getToken()
    });
  }

  // 1-2. state network true
  store.commit(UPDATE_NETWORK, true);

  // 2-1.  document  title
  !to.meta.isDynamicTitle &&
    to.meta.title &&
    (document.title = `${to.meta.title} - UXDCode`);
  // 2-2.  document  title vuex 
  //  beforeRouteEnter  UPDATE_TITLE UPDATE_TITLE  document  title

  // 3. /
  const loginPath = isAdmin ? '/admin/login' : '/login';
  //  vuex  token
  const token = isAdmin
    ? store.state.Auth.adminToken
    : store.state.Auth.userToken;
  //  localStorage / sessionStorage  token
  const token2 = isAdmin ? getToken('adminToken') : getToken();
  // console.log(loginPath, token);
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 
    // 
    if (!token || !token2) {
      // vuex  localStorage / sessionStorage  token
      //  vuex  token localStorage / sessionStorage  token
      // 

      // console.log(getToken('adminToken'), typeof getToken('adminToken'));  // null object
      // console.log(getToken(), typeof getToken());  // null object
      // console.log(token, typeof token);  // '' string
      next({
        path: loginPath,
        query: { redirect: to.fullPath } //  fullPath  
        // 
        // 1. 
        // 2.  fullPath 
      });
    } else {
      // vuex  token
      next();
    }
  } else {
    next(); //  next()
  }
});

/**
 * 
 * 0.  loading...
 */
router.afterEach((to, from) => {
  // 0-2.  loading... 
  setTimeout(() => {
    store.state.overlay = false;
    // store.state.loading = false;
    store.state.skeletonLoader = false;
  }, 200);
});

export default router;
