/**
 * HomeAdmin 
 */
import base from '@/api/base'; // 
import http from '@/utils/http'; // httpaxios
import { GetNavMenuItemsParams } from '@/types/layouts/baseLayoutAdmin.interface';
// import Qs from 'qs'; // qspost

const homeAdmin = {
  // 
  getNavMenuItems(params: GetNavMenuItemsParams) {
    return http.get(`${base.v1}/admin/menu`, {
      params: params
    });
  }
};

export default homeAdmin;
