/**
 * 
 */
import { DEV_BASE_API, PROD_BASE_API, TEST_BASE_API } from '@/config'; // API

// // .env.[mode] 
// const _baseAPI = process.env.VUE_APP_BASE_API;

// 
let _baseAPI = '';
if (process.env.NODE_ENV === 'development') {
  // 
  _baseAPI = DEV_BASE_API;
} else if (process.env.NODE_ENV === 'production') {
  // 
  _baseAPI = PROD_BASE_API;
} else if (process.env.NODE_ENV === 'test') {
  // 
  _baseAPI = TEST_BASE_API;
}

// 
const base = {
  v1: `${_baseAPI}/v1.0`,
  v2: `${_baseAPI}/v2.0`,
  'v2-1': `http://localhost:5000/api/v2.1`,
  v3: `${_baseAPI}/v3.0`
};

export default base;
